.feature {
    position: relative;
}

.feature__teams {
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: stretch;
        align-items: stretch;
}

.span {
    font-size: 0.7em;
    white-space: nowrap;
}

.large {
    font-size: 24px;
    line-height: 24px;
}

.feature__home_team,
.feature__away_team {
    width: 30%;
    min-width: 30%;
    color: #fff;
    text-align: center;
    padding: 15px 0;
}

.feature__team_logo {
    width: 100%;
}

.feature__team_title {
    margin: 0;
    padding: 10px;
}

.feature__match_details {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 30%;
    right: 30%;
    /* transform: translate(-50%); */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    background: rgba(255, 255, 255, 0.75);
    padding: 15px;
    text-align: center;
}

.feature__team_spacer {
    min-width: 206px;
    width: 40%;
}

.feature::after {
    position: absolute;
    top: 50%;
    left: 50%;
    font-size: 1.5rem;
    color: #fff;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

@media screen and (max-width: 575px) {
    .feature__teams {
        position: relative;
    }
    .feature__teams::after {
        content: '-vs-';
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
            -ms-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        color: #fff;
        font-size: 1.5rem;
    }
    .feature__team_spacer {
        display: none;
    }
    .feature__match_details {
        position: initial;
        -webkit-transform: translate(0);
            -ms-transform: translate(0);
                transform: translate(0);
    }
    .feature__item + .feature__item {
        margin-top: 10px;
    }
}


